import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import DailyOpenTime from '../views/report/opentime/DailyOpenTime.vue'
import PreOpenTime from '../views/report/opentime/PreOpenTime.vue'
import DiworkOpenTime from '../views/report/opentime/DiworkOpenTime.vue'
import DailyWebUI from '../views/report/webui/DailyWebUI.vue'
import PreWebUI from '../views/report/webui/PreWebUI.vue'
import DiworkWebUI from '../views/report/webui/DiworkWebUI.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
	children:[
		{
		  path: '/DailyOpenTime',
		  name: 'DailyOpenTime',
		  component: DailyOpenTime
		},
		{
		  path: '/PreOpenTime',
		  name: 'PreOpenTime',
		  component: PreOpenTime
		},
		{
		  path: '/DiworkOpenTime',
		  name: 'DiworkOpenTime',
		  component: DiworkOpenTime
		},
		{
		  path: '/DailyWebUI',
		  name: 'DailyWebUI',
		  component: DailyWebUI
		},
		{
		  path: '/PreWebUI',
		  name: 'PreWebUI',
		  component: PreWebUI
		},
		{
		  path: '/DiworkWebUI',
		  name: 'DiworkWebUI',
		  component: DiworkWebUI
		}
	]
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
